.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding-block: 24px;
  @include contentPaddingInline;

  @include onTable {
    padding-block: 32px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 16px;

    @include hover(transform, scale(1.2));

    @include onTable {
      height: 24px;
    }
  }
}
